import React from 'react'
import PropTypes from 'prop-types'

import './card.scss'

const Card = ({ onClick, className, children }) => (
  <div onClick={onClick} className={`card ${className}`}>
    {children}
  </div>
)
 
Card.defaultProps = {
  onClick: () => {},
  className: '',
}

Card.propTypes = {
  onClick: PropTypes.func,
  className: PropTypes.string,
}

export default Card