import React, { Component } from 'react'
import PropTypes from 'prop-types'

import './skill.scss'

class Skill extends Component {
  state = {
    isSelected: this.props.isSelected,
    disabled: this.props.disabled,
  }

  // Toggle selected state
  toggle = () =>
    !this.props.disabled && this.setState({ isSelected: !this.state.isSelected }, () => {
      const { onSkillSelect } = this.props
      onSkillSelect(this.props.title, this.state.isSelected)
    }
  )

  render() {
    const skillClass = ['skill']

    // State classes
    if (this.state.isSelected) {
      skillClass.push('skill--selected')
    }
    if (this.state.disabled) {
      skillClass.push('skill--disabled')
    }

    return (
      <div onClick={this.toggle} className={skillClass.join(' ')}>
        <p className="skill__label g--margin-none g--font-body-bold">{this.props.label}</p>
      </div>
    )
  }
}
 
Skill.defaultProps = {
  className: '',
  onSkillSelect: () => {},
  isSelected: false,
  disabled: false,
  label: '',
}

Skill.propTypes = {
  className: PropTypes.string,
  onSkillSelect: PropTypes.func,
  disabled: PropTypes.bool,
  isSelected: PropTypes.bool,
  label: PropTypes.string,
}

export default Skill