import React from 'react'
import PropTypes from 'prop-types'
import Card from '../card'
import FontAwesome from '../font-awesome'

import './answer.scss'

const Answer = ({ className, label, icon, iconType }) => (
  <Card className={`answer ${className}`}>
    <FontAwesome className="answer__icon" icon={icon} type={iconType} />
    <p className="answer__label g--margin-none">{label}</p>
  </Card>
)
 
Answer.defaultProps = {
  className: '',
  label: '',
  icon: '',
  iconType: '',
}

Answer.propTypes = {
  className: PropTypes.string,
  label: PropTypes.string,
  icon: PropTypes.string,
  iconType: PropTypes.string,
}

export default Answer